
.sort-wrap {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  .top-arrow {
    display: inline-block;
    position: relative;
    width: 0;
    height: 0;
    border: 0.12rem solid transparent;
    border-bottom-color: #999;
    border-top-width: 0;
  }
  .bottom-arrow {
    display: inline-block;
    position: relative;
    width: 0;
    height: 0;
    border: 0.12rem solid transparent;
    border-top-color: #999;
    border-bottom-width: 0;
    margin-top: 0.06rem;
  }
  .arrow-down {
    border-top-color: #f34a55;
  }
  .arrow-up {
    border-bottom-color: #f34a55;
  }
}
