
// 隐藏滚动条
::-webkit-scrollbar {
  display: none;
}
.list-wrap {
  position: relative;
  width: 100%;
  max-height: 100%;
  overflow-x: auto;
  overflow-y: auto;
  .content-wrap {
    display: inline-block;
    table {
      font-size: 0.24rem;
      white-space: nowrap;
      padding-top: 0.8rem;
      thead {
        tr {
          th {
            position: sticky;
            top: 0;
            background: #fff;
            height: 0.8rem;
            color: #999;
            font-weight: 500;
            padding: 0;
            box-sizing: border-box;
            z-index: 2;
            &:first-child {
              left: 0;
              z-index: 3;
            }
          }
        }
      }
      tbody {
        tr {
          td {
            height: 0.96rem;
            box-sizing: border-box;
            background: #fff;
            &:first-child {
              position: sticky;
              left: 0;
              z-index: 1;
            }
          }
        }
      }
      .th__cell {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 0 0.2rem;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        box-sizing: border-box;
        .sort__icon {
          position: relative;
          margin-left: 0.1rem;
        }
      }
      .td__cell {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 0 0.2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        color: #999;
        .stock-name {
          color: #333;
          font-size: 0.28rem;
          font-weight: 600;
          margin-bottom: 0.12rem;
        }
      }
    }
  }
  .before-trigger {
    position: sticky;
    left: 0;
    width: 100%;
    font-size: 0.28rem;
    color: #999;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
  }
}
